.partner-news {
  background-color: #fff;
  margin-top: 15px;
  background: #fff;
  padding: 20px 20px 0;
  border-bottom: 1px solid transparent;
}
@media (max-width: 1024px) {
  .partner-news {
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .partner-news {
    margin: 20px -16px 0 -16px;
  }
}
.partner-news__title {
  margin: 10px 0 20px 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 27px;
}